:root {
  --primary-text-color: #2c2c2c;
  --primary-white-color: #fefefe;
  --button-green: #bbf330;
  --yellow-main: #ffe550;
  --dark-green: #597d00;
  --cubic: cubic-bezier(.4, 0, .2, 1);
}

.page {
  color: var(--primary-text-color);
  letter-spacing: .03em;
  font-family: Roboto, sans-serif;
}

h1, h2, h3, p, ul {
  margin: 0;
  padding: 0;
}

img {
  height: auto;
  width: 100%;
  display: block;
}

.list {
  list-style: none;
}

.link {
  text-decoration: none;
}

button {
  margin: 0;
  padding: 0;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 319.98px) {
  .container {
    width: 320px;
  }
}

@media screen and (min-width: 767.98px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1279.98px) {
  .container {
    width: 1240px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.section {
  padding-top: 60px;
  padding-bottom: 60px;
}

@media screen and (min-width: 1279.98px) {
  .section {
    padding-top: 100px;
    padding-bottom: 100px;
  }
}

.favorites__title {
  text-align: center;
  margin-bottom: 20px;
  font-size: 28px;
  font-weight: 700;
  line-height: 1.18;
}

@media screen and (min-width: 767.98px) {
  .favorites__title {
    font-size: 42px;
    line-height: 1.19;
  }
}

.header {
  background-color: var(--primary-white-color);
}

.header__container {
  height: 72px;
  justify-content: space-between;
  align-items: center;
  display: flex;
  box-shadow: 0 4px 24px rgba(105, 105, 105, .1);
}

.logo__link {
  width: 66px;
  height: 52px;
}

.menu {
  margin-left: auto;
  margin-right: 30px;
  list-style: none;
  display: flex;
}

@media screen and (max-width: 767.98px) {
  .menu {
    display: none;
  }
}

.menu__item {
  padding-top: 28px;
  padding-bottom: 28px;
}

.menu__item + .menu__item {
  margin-left: 28px;
}

.menu__link {
  color: inherit;
  transition: color 250ms var(--cubic), text-decoration 250ms var(--cubic);
  font-size: 14px;
  font-weight: 400;
  line-height: 1.14;
  text-decoration: none;
}

.menu__item:hover .menu__link, .menu__item:focus .menu__link {
  color: var(--button-green);
  text-decoration: underline;
}

.button__order {
  width: 98px;
  height: 32px;
  color: var(--primary-text-color);
  background-color: var(--button-green);
  transition: background-color 250ms var(--cubic);
  border: none;
  border-radius: 30px;
  font-size: 13px;
  font-weight: 500;
  line-height: 1.5;
  display: block;
}

.button__order:hover, .button__order:focus {
  background-color: #b5ff00;
}

@media screen and (max-width: 767.98px) {
  .button__order {
    display: none;
  }
}

.js-open-menu {
  width: 28px;
  height: 28px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
}

@media screen and (min-width: 767.98px) {
  .js-open-menu {
    display: none;
  }
}

.menu__container {
  width: 130px;
  background: var(--primary-white-color);
  pointer-events: none;
  opacity: 0;
  transition: transform 250ms var(--cubic), opacity 250ms var(--cubic);
  border-radius: 30px 0 30px 30px;
  position: relative;
  top: -22px;
  left: 51%;
  overflow: hidden;
  transform: rotateY(-90deg);
  box-shadow: 0 4px 24px rgba(105, 105, 105, .1);
}

.menu__container.is__open {
  opacity: 1;
  pointer-events: all;
  transform: rotateY(0);
}

@media screen and (min-width: 767.98px) {
  .menu__container {
    display: none;
  }
}

.button__close {
  width: 24px;
  height: 24px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  position: absolute;
  top: 4px;
  right: 4px;
}

.mobile-menu__item {
  list-style: none;
}

.mobile-menu__link {
  color: inherit;
  transition: background-color 250ms var(--cubic);
  padding: 16px;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.19;
  text-decoration: none;
  display: block;
}

.mobile-menu__link:active, .mobile-menu__link:hover, .mobile-menu__link:focus {
  background-color: var(--button-green);
}

:focus-visible {
  outline: 2px double var(--button-green);
  outline-offset: 1.5px;
}

.favorites__subtitle {
  text-align: center;
  margin-bottom: 60px;
  font-size: 14px;
  line-height: 1.14;
}

@media screen and (min-width: 767.98px) {
  .favorites__subtitle {
    font-size: 16px;
    line-height: 1.19;
  }
}

.gallery-list__item {
  border: 11px solid var(--yellow-main);
  border-radius: 139.658px;
  padding: 18.12px 17.35px;
}

.gallery-list__item:hover, .gallery-list__item:focus {
  border-color: #bbf330;
}

@media screen and (min-width: 767.98px) {
  .gallery-list__item {
    border: 14px solid var(--yellow-main);
    border-radius: 167.59px;
    padding: 20.91px 20.03px;
  }
}

@media screen and (min-width: 1279.98px) {
  .gallery-list__item {
    border: 16px solid var(--yellow-main);
    border-radius: 197px;
    padding: 25px 24px;
  }
}

.favorites__gallery {
  flex-direction: column;
  gap: 20px;
  display: flex;
}

@media screen and (min-width: 767.98px) {
  .favorites__gallery {
    flex-flow: wrap;
    flex-basis: calc(50% - 15px);
    justify-content: center;
    align-items: center;
    gap: 32px;
  }
}

@media screen and (min-width: 1279.98px) {
  .favorites__gallery {
    flex-basis: calc(33.3333% - 21.3333px);
    gap: 32px;
  }
}

.gallery-list__photo {
  border-radius: 116.323px;
}

@media screen and (min-width: 767.98px) {
  .gallery-list__photo {
    border-radius: 133.648px;
  }
}

@media screen and (min-width: 1279.98px) {
  .gallery-list__photo {
    width: 315px;
    height: 315px;
    border-radius: 157.5px;
  }
}

.gallery-list__item:nth-child(n+4) {
  display: none;
}

@media screen and (min-width: 767.98px) {
  .gallery-list__item:nth-child(n+4) {
    display: flex;
  }
}

